import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../user';
import { UserService } from '../user.service';
import { TodoService } from '../todo.service';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
  providers: [],
})
export class MainComponent{  
  username;  
  todos;
  selectedTodo;
  checkIcon = 'http://127.0.0.1:8000/media/check.png';
  
  
  constructor(
    private todoService: TodoService,
    private appUser: UserService,
    public user: User,
    private router: Router,
    )
    {
      if (sessionStorage.getItem('currentUser') != null)
      {
        this.appUser.getCurrentUser();
        this.appUser.setLoggedIn(true);
        this.getTodos();
        this.selectedTodo = {title: ''};
      }  
      else
      { 
        this.appUser.setLoggedIn(false);
        this.router.navigate(['']);
      }
    }
    

    getTodos() {
      if (this.user.isSuperuser)
      {
        this.todoService.getAllTodos().subscribe(
          data => {
            this.todos = data;
          },
          error => {
            console.log(error);
          }
        );
      }
      else
      {
        this.todoService.getTodosByUser(this.user.id).subscribe(
          data => {
            this.todos = data;
          },
          error => {
            console.log(error);
          }
        );
      }
    }
  
    todoClicked(todo) {
      this.todoService.getTodo(todo.id).subscribe(
        data => {
          this.selectedTodo = data;
          this.appUser.getUsername(this.selectedTodo.user)
            .subscribe(
              data => {
                this.username = data[0].username.toString();
              }
            )
        },
        error => {
          console.log(error);
        }
      );
      }
      
    completeTodo() {
      this.todoService.completeTodo(this.selectedTodo, true)
        .subscribe(
          data => {
            this.getTodos();
            this.selectedTodo = {title: ''};
          },
          error => {            
            console.log(error);
          }
        );
    }

    inCompleteTodo() {
      this.todoService.completeTodo(this.selectedTodo, false)
        .subscribe(
          data => {
            this.getTodos();
            this.selectedTodo = {title: ''};
          },
          error => {            
            console.log(error);
          }
        );
    }

    updateTodo() {
      this.todoService.updateTodo(this.selectedTodo, 
        this.user.id).subscribe(
        data => {
          this.getTodos();
          this.selectedTodo = {title: ''};
        },
        error => {            
          console.log(error);
        }
      );
    }
  
    createTodo() {
      if(this.selectedTodo.title.length < 8)
      {
        alert('Please input what you want to do, '
          + 'it needs to be at least 8 characters')
        console.log('Minimum length is 8');
      }
      else
      {
        this.todoService.createTodo(this.selectedTodo.title, 
          this.user.id).subscribe(
          data => {
            this.todos.push(data);
            this.selectedTodo = {title: ''};
          },
          error => {
            console.log(error);
          }
        );
      }
    }

    deleteTodo() {
      this.todoService.deleteTodo(this.selectedTodo.id).subscribe(
        data => {
          this.getTodos();
          this.selectedTodo = {title: ''};
        },
        error => {
          console.log(error);
        }
      );
    }


}

